import React, { Component, useEffect, useState } from 'react'
import './Findload.css'
import { useHistory } from "react-router-dom";
import { ToastContainer, toast } from 'react-toastify';
import Autocomplete from '@mui/material/Autocomplete';


export default function Findload() {




    const history = useHistory();

    useEffect(() => {
        let date = new Date();
        let day = date.getDate();
        let month = date.getMonth() + 1;
        let year = date.getFullYear();
        let current = month + "-" + day + "-" + year;
        if (localStorage.getItem('email')) {
            if (localStorage.getItem("userType") == "Broker" || localStorage.getItem("userType") == "Carrier") {
                let subscriptionDate1 = localStorage.getItem("Subscription Date");

                var currentDate = new Date(current);
                var SubscriptionDate2 = new Date(subscriptionDate1);



                // To calculate the time difference of two dates
                var Difference_In_Time = currentDate.getTime() - SubscriptionDate2.getTime();

                // To calculate the no. of days between two dates
                var Difference_In_Days = Difference_In_Time / (1000 * 3600 * 24);
                console.log(Difference_In_Days);
                if (Difference_In_Days > 30) {
                    console.log("Yes Over");
                    history.push('./login');
                }
                else {
                    history.push('./postload');
                }



            }
            else {
                history.push('./postload');
            }



        }
        else {
            history.push('./login');

        }
    }, [])

    const [vehicle, setVehicle] = useState();
    const [pointOfLoading, setPointOfLoading] = useState();
    const [pointOfDelivery, setPointOfDelivery] = useState();
    const [productWeight, setProductWeight] = useState();
    const [timeOfLoading, setTimeOfLoading] = useState();
    const [phoneNo, setPhoneNo] = useState();



    const onchangePointOfDelivery = (event) => {
        setPointOfDelivery(event.target.value);
    }
    const onchangePhoneNo = (event) => {
        setPhoneNo(event.target.value);
    }
    const onchangeTimeOfLoading = (event) => {
        setTimeOfLoading(event.target.value);
    }

    const onchangeProductWeight = (event) => {
        setProductWeight(event.target.value);
    }

    const onchangePointOfLoading = (event) => {
        setPointOfLoading(event.target.value);
    }



    const onchangeVehicle = (event) => {
        setVehicle(event.target.value);
    }
    return (
        <div className='FindLoadModule'>
            <div className='container'>
                <h1 id='FindLoad-h1'>Post a Load</h1>

                <form id='postLoadForm'>
                    <input type='text' placeholder='Type of Vehicle' name='VehicleType' onChange={onchangeVehicle} value={vehicle} id="name" />
                    <input type='text' placeholder='Point of Loading' name='PointOfLoading' onChange={onchangePointOfLoading} value={pointOfLoading} id="location" />
                    <input type='text' placeholder='Point of Delivery' name='Point of Delivery' onChange={onchangePointOfDelivery} value={pointOfDelivery} id="shipper-name" />
                    <input type='text' placeholder='Product Weight' name='ProductWeight' onChange={onchangeProductWeight} value={productWeight} id="product-weight" />
                    <label for="TimeOfLoading" id='label-time-of-loading'>TimeOfLoading</label>
                    <input type='time' placeholder='Time of Loading' name='TimeOfLoading' onChange={onchangeTimeOfLoading} value={timeOfLoading} id="time-of-loading" />
                    <input type='tel' placeholder='Phone No' name='PhoneNumber' onChange={onchangePhoneNo} value={phoneNo} id="phone-no" />
                    <input type="submit" id="Submit" value="Submit" />



                </form>


            </div>
            <ToastContainer />


        </div>
    );
}