import React, { useState } from "react";
import axios from "axios";
import { ToastContainer, toast } from 'react-toastify';
import { Link } from "react-router-dom";
import { useHistory } from "react-router-dom";
import { useEffect } from "react";


import './Login.css'
import { Button } from "@mui/material";



export default function Login() {

    const history = useHistory();
    




    useEffect(() => {
        let date = new Date();
        let day = date.getDate();
        let month = date.getMonth() + 1;
        let year = date.getFullYear();
        let current = month + "-" + day + "-" + year;

        if (localStorage.getItem('email')) {
            if (localStorage.getItem("userType") == "Broker" || localStorage.getItem("userType") == "Carrier") {
                let subscriptionDate1 = localStorage.getItem("Subscription Date");

                var currentDate = new Date(current);
                var SubscriptionDate2 = new Date(subscriptionDate1);



                // To calculate the time difference of two dates
                var Difference_In_Time = currentDate.getTime() - SubscriptionDate2.getTime();

                // To calculate the no. of days between two dates
                var Difference_In_Days = Difference_In_Time / (1000 * 3600 * 24);
                console.log(Difference_In_Days);
                if (Difference_In_Days > 30) {
                    console.log("Yes Over");
                   history.push('./login');
                   setTimeout(function () {
                        toast.error("Your Subscription was only for one month", {
                            position: "top-right"
                       })
                       toast.error("You need to pay to get your Subscription", {
                            position: "top-right"
                        })
                    }, 800)
                    localStorage.removeItem("Subscription Date");
                    localStorage.removeItem("email");
                    localStorage.removeItem("userType");

                }
                else {
                    history.push('./postload');
                }

            }
            else {
                history.push('./postload');
            }

        }
        else{
            history.push('./login');
        }


    



    }, [])


const [status, setStatus] = useState("null");
const [nameLogin, setLoginName] = useState();
const [passwordLogin, setLoginPassword] = useState();
const [disabledValue, setDisabled] = useState(true);
const url = `http://localhost:8080/dologin`;


const onchangeLoginName = (event) => {
    setLoginName(event.target.value);
    if (document.getElementById("name-login").value != 0) {
        if (document.getElementById("password-login").value != 0) {
            setDisabled(false);
        }
        else {
            setDisabled(true);
        }

    }
    else {

        setDisabled(true);



    }
}

const onchangeLoginPassword = (event) => {
    setLoginPassword(event.target.value);
    if (document.getElementById("name-login").value != 0) {
        if (document.getElementById("password-login").value != 0) {
            setDisabled(false);
        }
        else {
            setDisabled(true);
        }

    }
    else {

        setDisabled(true);
    }
}
function LoginHandler(e) {
    e.preventDefault();


    let tempStatus;
    let check = 0;

    // let signupDate=new Date();






    // console.log(DiffInDays);

    if (document.getElementById("name-login").value == 0) {
        tempStatus = "error"
        setStatus(tempStatus);
        if (status == "error") {
            toast.error("Not Registered", {
                position: "top-right"
            })
        }
        toast.error("Enter Name", {
            position: "top-right"
        });
    }
    else if (document.getElementById("password-login").value == 0) {
        tempStatus = "error"
        setStatus(tempStatus);
        if (status == "error") {
            toast.error("Not Registered", {
                position: "top-right"
            })
        }
        toast.error("Enter Password", {
            position: "top-right"
        });
    }
    else {

        axios.post(url, {
            name: nameLogin,
            password: passwordLogin,


        })
            .then((res) => {
                console.log(res);
                console.log("Here");
                if (res.data[0] == "Login Successfull") {

                    tempStatus = "success";
                    console.log("success")
                    setStatus(tempStatus);

                    toast.success("Successfull Logged", {
                        position: "top-right"
                    })
                    localStorage.setItem("email", res.data[1]);
                    localStorage.setItem("userType", res.data[2]);
                    localStorage.setItem("Subscription Date", res.data[3]);
                    setTimeout(function () {
                        window.location.href = "/postload";
                    }, 2000)

                }
                else {
                    toast.error("Enter correct Email or Password", {
                        position: "top-right"
                    })
                }


            })


    }
}
return (
    <div id="Login-Module">
        <h1>Login!!</h1>
        <form>
            <input type="text" placeholder="Username" name="username" id="name-login" value={nameLogin} onChange={onchangeLoginName} />
            <input type="password" placeholder="Password" name="password" id="password-login" value={passwordLogin} onChange={onchangeLoginPassword} />
            <Link to="/signup" id="href-signup">Signup?</Link>
            <Button value="Login" onClick={LoginHandler} variant="outlined" color="primary" id="Login-Submit" disabled={disabledValue}>Login</Button>

        </form>
        <ToastContainer />
    </div>);


}
