import React from 'react';
import './Shipper.css';
import { useState } from 'react';
import Button from '@mui/material/Button';
import ButtonGroup from '@mui/material/ButtonGroup';
import { Link, Route } from 'react-router-dom';
import GetAllShipper from './GetAllShipper';
import PostLoadNavBar from './PostLoadNavBar';
import { Switch } from 'react-router-dom';
import UpdateShipper from './UpdateShipper';



export default function Shipper() {

    const [shipper, setShipper] = useState();
    const [shipperCompany, setShipperCompany] = useState();
    const [shipperLocation, setShipperLocation] = useState();

    const onchangeShipperLocation = (event) => {
        setShipperLocation(event.target.value);
    }

    const onchangeShipperCompany = (event) => {
        setShipperCompany(event.target.value);
    }
    const onchangeShipper = (event) => {
        setShipper(event.target.value);
    }



    return (
        <>
         <div id='ShipperModule'>
            <div id="container-shipper">
                <ButtonGroup variant="outlined" aria-label="outlined button group" id="ButtonGroupAll">
                    <Button id='buttonGroup1'color='primary' variant='outlined'><Link to="/shipper/getallshipperdetails" id='link1'>Get All Shipper Details</Link></Button>
                    <Button id='buttonGroup2'color='warning' variant='outlined'><Link to="/shipper/updateshipper" id='link2'>Update Shipper</Link></Button>
                    <Button id='buttonGroup3' color='error' variant='outlined'><Link to="/shipper/deleteshipper" id="link3">Delete Shipper</Link></Button>
                </ButtonGroup>
                <h1 id='Shipper-h1'>Shipper</h1>
                <input type='text' placeholder='Shipper Name' name='ShipperName' onChange={onchangeShipper} value={shipper} id="shipper-module-name" />
                <input type='text' placeholder='Product Name' name='ProductName ' onChange={onchangeShipperCompany} value={shipperCompany} id="shipper-module-company" />
                <input type='text' placeholder='Phone No' name='PhoneNo' onChange={onchangeShipperLocation} value={shipperLocation} id="shipper-module-location" />

            </div>

        </div>
     
        <Switch>
            <Route path="/shipper/getallshipperdetails" component={GetAllShipper}><GetAllShipper/></Route>
            <Route path="/shipper/updateshipper" component={UpdateShipper}><UpdateShipper/></Route>
        </Switch>
        
        </>
       
    );
}